body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.header {
    display: grid;
    grid-template-columns: 100px 1fr 100px;
    grid-template-rows: 150px;
    /* background-color: lightblue; */
    color: white;
    grid-column: 1/-1;
    z-index: 1000;
    font-size: 18pt;
    align-self: center;
    place-items: center;
    top: 0;
  }
  
@font-face {
  font-family: fr-bold;
  src: url(/futurama/static/media/fr-bold.3d20efac.ttf);
}

#goodNews {
  grid-column: 2/3;
  text-align: center;
  /* font-family: fr-bold; */
  padding-top: 300px;
}

h2 {
  margin: 0;
  font-family: fr-bold;
}

p {
  margin: 0;
  font-family: fr-bold;
  font-size: 16pt;
}

  /* #correct {
    grid-column: 2/3;
  } */
  
  #score {
    grid-column: 3/-1;
    margin-right: 25px;
    font-family: fr-bold;
    font-size: 12pt;
    /* padding-top: 150px; */

  }
  
img {
  height: 200px;
  margin: 5px;
}

.zoom {
  -webkit-transition: .2s;
  transition: .2s;
}

.zoom:hover {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}
.wrapper {
    background-image: url(/futurama/static/media/mars_background1.baa98ce7.jpg);
    height: 100vh;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

.imageGrid {
    /* display: grid; */
    /* width: 600px; */
    margin: 0 auto;
    margin-top: 25rem;
    grid-column: 2/6;
    grid-row: 1/3;
    text-align: center;
}
